.loader {
    background: $cp2;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all $t2;
    z-index: 100;

    &.is-complete {
        opacity: 0;
        pointer-events: none;
    }
}



